<template>
    <!---->
    <div v-loading="loading">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="page-toolbar clear m-t-sm">
                    <search-ipts :options="searchOptions" @submit="doSearch" v-show="searchShow"></search-ipts>
                    <h5 class="text-center text-bold text-xxlg">{{searchOptions[0].value}}{{searchOptions[1].value}}</h5>
                </div>
                <div class="page-contaoner">
                    <div class="lk-table m-t-sm">
                        <ul class="table-thead clear">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('5%')!=-1">5%</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('3.3%')!=-1">3.3%</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        </ul>
                        <ul class="table-tbody clear">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="data.code">{{data.code}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="data.name">{{data.name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="data.region_name">{{data.region_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="data.city_name">{{data.city_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="data.upper_agent">{{data.upper_agent}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="data.cinema_num">{{data.cinema_num}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('5%')!=-1":title="data.hall_num">{{data.hall_num}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('3.3%')!=-1":title="data.device_num">{{data.device_num}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="data.create_time">{{data.create_time}}</li>
                        </ul>
                        <ul class="table-tbody clear" v-for="(item, index) in data.items">
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1" :title="item.id">{{item.id}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.code">{{item.code}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.name">{{item.name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.region_name">{{item.region_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.city_name">{{item.city_name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.upper_agent">{{item.upper_agent}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.cinema_num">{{item.cinema_num}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('5%')!=-1":title="item.hall_num">{{item.hall_num}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('3.3%')!=-1":title="item.device_num">{{item.device_num}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1":title="item.create_time">{{item.create_time}}</li>
                        </ul>
                        <ul class="table-tbody clear" v-if="data.items.length===0">
                            <li class="p-n over-omit"></li>
                        </ul>
                    </div>
                    <div class="footer clear m-t-md">
                        <div class="pull-left news-record text-lg">
                            <span v-show="data.items.length>0">{{offset + 1}}{{offset + data.items.length}}</span><span
                            class="text-blue">{{data.total}}</span> 
                            <el-select v-model="limit" placeholder="" style="width: 66px;">
                                <el-option
                                    v-for="(item,index) in options"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                            
                        </div>
                        <div class="pull-right">
                            <el-pagination
                                background
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-count="pages">
                            </el-pagination>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader
        },
        data: () => ({
            data: {
                limit: 10,
                page: 1,
                pageSize: 1,
                total: 1,
                items: []
            },
            yearList: [],
            monthList: [],
            loading: false,
            selectVal: ['', '', '', '', '', '', '', '5%', '3.3%', ''],
            showList: ['', '', '', '', '', '', '', '5%', '3.3%', ''],
            options: [10, 25, 50],   //
            searchShow: true,   //
            limit: 10,
            page: 1,
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'financial_statistics'
                    }
                },
                childNode: {
                    name: '',
                    desc: '',
                    router: {
                        name: 'financial_report'
                    }
                }
            },
            //
            searchOptions: [
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: []
                },
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: []
                }
            ],
        }),
        computed: {
            //
            pages () {
                return this.data.pageSize || 1
            },
            offset () {
                return (this.page - 1) * this.limit
            }
        },
        methods: {
            //
            getList () {
                this.loading = true
                this.$http.post(api.agent.list, {
                    name: this.searchOptions[0].value,
                    pid: this.searchOptions[1].value,
                    page: this.page,
                    limit: this.limit
                }).then(res => {
                    this.selectedGroup = []
                    let that = this
                    setTimeout(function () {
                        that.loading = false
                    }, 500)
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = {
                            limit: 10,
                            page: 1,
                            pageSize: 1,
                            total: 1,
                            items: []
                        }
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            getOptions () {
                let year = new Date().getFullYear()
                let month = new Date().getMonth() + 1
                for (let i = 2000; i <= year; i++) {
                    this.searchOptions[0].options.push({
                        label: i + '',
                        value: i
                    })
                }
                for (let i = 1; i < 13; i ++) {
                    this.searchOptions[1].options.push({
                        label: i + '',
                        value: i
                    })
                }
                this.searchOptions[0].value = year
                this.searchOptions[1].value = month
            },
            //
            refresh () {
                this.getList()    //
            },
            // 
            doSearch (data) {
                this.page = 1
                this.searchOptions = data
                this.getList()
            },
            // 
            addPage () {
                if (this.page < this.pages) this.page += 1
            },
            // 
            delPage () {
                if (this.page > 1) this.page -= 1
            }
        },
        created () {
            this.getOptions()
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
            this.getList()
        },
        watch: {
            page (val) {
                this.$router.replace({name: 'financial_report', query: {page: val}})
                this.getList()
            },
            limit (val) {
                this.getList()
            }
        }
    }
</script>
<style scoped>
</style>



